<!-- 百度地图示例页面 -->
<template>
    <div class="experience-main">
        <ExperienceEditor :baseCode="baseCode" style="width: 40%;height: 100%;"></ExperienceEditor>
        <div ref="iframeContent" style="flex: 1;padding: 5px;box-sizing: border-box;overflow: hidden;">
        </div>
    </div>
</template>
  
<script>
import ExperienceEditor from './ExperienceEditor.vue';
export default {
    components: { ExperienceEditor },
    props:{
        baseCode:String // 原始code
    },
    methods: {
        // 向iframe空页面写代码
        writeCode(code) {
            if(!code){
                return;
            }
            const iframe = window.document.createElement('iframe');
            iframe.style = "height: 100%;width: 100%;overflow: hidden;border: unset;";
            iframe.src = "about:blank#";
            this.$nextTick(()=>{
                const iframeContent = this.$refs.iframeContent;
                iframeContent.innerHTML = '';
                iframeContent.appendChild(iframe);
                iframe.contentWindow.document.open();
                iframe.contentWindow.document.write(code);
                iframe.contentWindow.document.close();
            })
        }
    }
};
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
.experience-main {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
}
</style>
