 <!-- 代码编辑 -->
<template>
    <div class="experience-editor">
        <div class="experience-editor-header">
            <div class="experience-editor-header-title">html编辑器</div>
            <div class="experience-editor-header-right">
                <div class="experience-editor-header-right-box" v-clipboard:copy="code" v-clipboard:success="onCopy"
                    v-clipboard:error="onError">
                    <i class="el-icon-document-copy"></i>
                    复制
                </div>
                <div class="experience-editor-header-right-box" @click="play">
                    <i class="el-icon-video-play"></i>
                    运行
                </div>
                <div class="experience-editor-header-right-box" @click="refresh">
                    <i class="el-icon-refresh"></i>
                    刷新
                </div>
                <div class="experience-editor-header-right-box" @click="download('index.html', code)">
                    <i class="el-icon-download"></i>
                    下载
                </div>
            </div>
        </div>
        <div style="flex: 1;overflow: auto;">
            <codemirror ref="myCm" v-model="code" :options="cmOptions" @changes="onChanges" class="experience-editor-code"></codemirror>
        </div>
    </div>
</template>
  
<script>
import { codemirror } from 'vue-codemirror';
import 'codemirror/lib/codemirror.css';
import 'codemirror/theme/base16-dark.css'; // 引入主题样式，根据设置的theme的主题引入
import 'codemirror/mode/htmlmixed/htmlmixed.js'; // html代码高亮
export default {
    name: 'experience-editor',
    components: {
        codemirror
    },
    props:{
        baseCode:String // 原始code
    },
    watch:{
        baseCode:{
            immediate: true,
            handler(newValue,oldValue){
                this.code = newValue;
                this.$parent.writeCode(newValue);
            }
        }
    },
    data() {
        return {
            code: '',// 展示code
            cmOptions: {
                tabSize: 4,
                mode: 'htmlmixed', // htmlcssjs可以用这个，其他语言可以看官网的参数
                theme: 'base16-dark', // 主题和引入对应
                lineWrapping: true, // 自动换行
                lineNumbers: true
            }
        };
    },
    methods: {
        //下载
        download(filename, text) {
            var element = document.createElement('a');
            element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
            element.setAttribute('download', filename);
            element.style.display = 'none';
            document.body.appendChild(element);
            element.click();
            document.body.removeChild(element);
        },
        play() {
            // 调用父组件的方法
            this.$parent.writeCode(this.code);
        },
        refresh() {
            this.code = this.baseCode;
            this.$parent.writeCode(this.baseCode);
        },
        // 复制成功时的回调函数
        onCopy(e) {
            this.$message.success('内容已复制到剪切板！'); // 使用的element-ui插件
        },
        // 复制失败时的回调函数
        onError(e) {
            this.$message.error('抱歉，复制失败！');
        },
        onChanges() {
            this.code = this.$refs.myCm.content;
        }
    }
};
</script>

<style lang='scss' scoped>
.experience-editor{
    display: flex;
    flex-direction: column;
}
.experience-editor-header {
    display: flex;
    height: 40px;
    line-height: 40px;
    background-color: #f3f4f8;
    justify-content: space-between;
    padding-left: 10px;
    font-size: 15px;
}

.experience-editor-header-right {
    display: flex;
    padding: 0 5px;
    user-select: none;

    .experience-editor-header-right-box {
        cursor: pointer;
        margin-right: 10px;
        &:hover{
            color: #0051ff;
        }
    }
}

.experience-editor-code {
    height: 100%;
}

::v-deep .CodeMirror {
    height: 100%;
}
</style>
  